.items{
    list-style-type: none;
    font-size: 30px;
    margin-right: 5%;
}
.delete-btn {
    margin-top: 10px;
    font-size: 20px;
    padding: 5px;
    cursor: pointer;
    margin-left: 20px;
    color: white;
    background-color: red;
}
.edit-btn {
    margin-top: 10px;
    width: 73px;
    font-size: 20px;
    padding: 5px;
    cursor: pointer;
    margin-left: 20px;
    color: white;
    background-color: green;
}

@media only screen and (max-width: 360px) {
    .items {
        font-size: 20px;
    }
    .delete-btn, .edit-btn {
        font-size: 15px;
    }
  }