body {
  background-color: rgb(146, 138, 138);
  
}



.container {
    text-align: center;
    font-family: sans-serif ,Courier, monospace;
    color: white;
    /* background-color: rgb(146, 138, 138); */
    background-color: black;
    border-radius: 20px;
    margin-left: 25%;
    margin-right: 25%;
    margin-top: 3%;
    padding: 8px;
    box-shadow: 0 0 20px #887978;
}
.heading {
font-size: 45px;
}


@media only screen and (max-width: 723px) {
    .container {
        margin-left: 20%;
        margin-right: 20%;
    }
  }
@media only screen and (max-width: 602px) {
    .container {
        margin-left: 15%;
        margin-right: 15%;
    }
  }
@media only screen and (max-width: 518px) {
    .container {
        margin-left: 10%;
        margin-right: 10%;
    }
  }
@media only screen and (max-width: 454px) {
    .container {
        margin-left: 5%;
        margin-right: 5%;
    }
  }
@media only screen and (max-width: 404px) {
    .container {
        margin-left: 0%;
        margin-right: 0%;
    }
  }